.myBtn {
    /* display: block;
    
    bottom: 20px;*/
    position: fixed;
    right: -97%;
    width: 100px;
    height: 100px;
    -webkit-animation: swing 2s ease;
    animation: swing 2s ease;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: infinite;
    /* -webkit-transform: scale(0.5);
    -ms-transform: scale(0.5);
    transform: scale(0.5); */

}
